import * as React from 'react';
import {
    Container,
    IconButton,
    Tabs,
    Tab,
    Typography,
    Box,
    Button,
    List,
    ListItem,
    ListItemButton,
    ListItemText,
    Stack,
    Paper,
    Card,
    CardHeader,
    CardContent,
    CardMedia,
    CardActions,
    useTheme,
    Grid,
    ImageListItem, Fade, ImageListItemBar, ListItemAvatar, ImageList, Icon
} from "@mui/material";
import Time from "@mui/icons-material/AccessTime";
import Divider from '@mui/material/Divider';
import {generatePath, Link, useNavigate, useParams} from "react-router";
import axios, {AxiosResponse} from "axios";
import {baseUrl, eventItemUrl, eventsUrl, excursionsUrl} from "../../config/consts";
import {IPageResponse} from "../../common/response";
import {useEffect, useState} from "react";
import {Header} from "../../components/header/header-component";
import {ApplicationBar} from "../../components/appbar/application-bar";
import {excursionItemRoute, placeRoute, topicRouteUrl} from "../../config/routes";
import {Excursion} from "../excursion-page/model";
import {Event} from "../../common/event";
import {Qr} from "../../components/qrcode/qr-code.component";
import {ReactComponent as Background} from "../../svg/background.svg";
import {ReactComponent as Background2} from "../../svg/background-2.svg";
import {EventItems} from "../events-page/event-items.components";

export interface EventItemPageProps {

}


export const EventItemPage = (props: EventItemPageProps) => {

    const navigate = useNavigate();

    const theme = useTheme();

    const id = useParams().id;


    const [data, setData] = useState<Event | null>(null);

    useEffect(() => {
        axios.get(generatePath(eventItemUrl, {id: id || ''}), {})
            .then((resp: AxiosResponse<Event>) => {
                setData(resp.data || null)
            })
            .catch((resp) => {
                navigate("/")
            });
    }, [])

    if (!data) {
        return null;
    }

    return <div>
        <div style={{position: "relative"}}>
            <div style={{
                opacity: 0.03,
                position: "absolute",
                height: "100%",
                width: "100%",
                backgroundImage: "url(https://sun1-16.userapi.com/impg/7MVdJ7k4B_ik2HbKWyM68zx4gtGqYCNN1kJjaQ/p1oqti9iNfs.jpg?size=1600x1068&quality=96&sign=7fc8494393646b3ff6e56456854acce9&type=album)"
            }}>
            </div>
            <ApplicationBar/>
        </div>

        <Stack spacing={4} sx={{
            [theme.breakpoints.down("md")]: {paddingTop: 4},
            [theme.breakpoints.down("sm")]: {paddingTop: 2},
            paddingTop: 4
        }}>
            <Paper  style={{background:"unset"}} sx={{backgroundColor: "unset"}}>
                <Container>
                    <Stack flexDirection={"row-reverse"} alignItems={"center"}>
                        {data.url ? <Box sx={{[theme.breakpoints.down("lg")]: {display: "none"}}}>
                            <Qr url={data.url}/>
                        </Box> : null}
                        <Stack spacing={4} sx={{
                            [theme.breakpoints.down("md")]: {paddingTop: 4},
                            [theme.breakpoints.down("sm")]: {paddingTop: 2},
                            flex: 1
                        }}>
                            <Typography variant={"h5"}>{data.title || ''}</Typography>
                            <EventItems price={data.price} place={data.place} date={data.dateStart} size={3}/>
                        </Stack>
                    </Stack>

                </Container>
            </Paper>
            <Paper sx={{paddingTop: 4, paddingBottom: 4, position: "relative"}}>
                <Box sx={{position: "absolute", top: 0, zIndex: 0, right: 0, bottom: 0, left: 0}}>
                    <Background style={{width: "100%", height: "100%"}}/>
                </Box>
                <Container>
                    <Stack flexDirection={"row"} sx={{}} paddingLeft={4} paddingRight={4}>
                        <ImageListItem sx={{flex: 1}}>
                            <Fade in={true}>
                                <img
                                    srcSet={data.image || ''}
                                    src={data.image || ''}
                                    width={"100%"}
                                    loading="lazy"
                                />
                            </Fade>
                        </ImageListItem>
                    </Stack>
                </Container>
            </Paper>
            <Paper  style={{background:"unset"}} sx={{paddingTop: 4, paddingBottom: 8, position: "relative", backgroundColor:"unset"}}>
                <Box sx={{position: "absolute", top: 0, zIndex: 0, right: 0, bottom: 0, left: 0}}>
                    <Background2 style={{width: "100%", height: "100%"}}/>
                </Box>
                <Container>
                    <Typography variant={"subtitle1"} color={"textSecondary"}>{(data.description || '').split("\n").map((it, idx) => <>
                        {idx > 0 && <br/>}
                        {it}
                    </>)}</Typography>
                </Container>
            </Paper>

        </Stack>

    </div>
}
