const mounths: string[] = [
  "января",
  "февраля",
  "марта",
  "апреля",
  "мая",
  "июня",
  "июля",
  "августа",
  "сентября",
  "октября",
  "ноября",
  "декабря",
];
export const formatDate = (dateStart: Date, dateEnd: Date) => {
  let hoursStart: string = dateStart.getHours().toString();
  let minutesStart: string = dateStart.getMinutes().toString();
  let dayStart: string = dateStart.getDate().toString();
  let mounthStart: string = mounths[dateStart.getMonth()];

  let hoursEnd: string = dateEnd.getHours().toString();
  let minutesEnd: string = dateEnd.getMinutes().toString();
  let dayEnd: string = dateEnd.getDate().toString();
  let mounthEnd: string = mounths[dateEnd.getMonth()];

  if (+minutesStart <= 9) minutesStart = "0" + minutesStart;
  if (+hoursStart <= 9) hoursStart = "0" + hoursStart;
  if (+dayStart <= 9) dayEnd = "0" + dayStart;

  if (+minutesEnd <= 9) minutesEnd = "0" + minutesEnd;
  if (+hoursEnd <= 9) hoursEnd = "0" + hoursEnd;
  if (+dayEnd <= 9) dayEnd = "0" + dayEnd;

  if (+dayEnd === +dayStart && dateEnd.getMonth() === dateStart.getMonth()) {
    return `${dayStart} ${mounthStart}, ${hoursStart}:${minutesStart}\u2013${hoursEnd}:${minutesEnd}`;
  } else {
    return `${dayStart} ${mounthStart}, ${hoursStart}:${minutesStart} \u2014 ${dayEnd} ${mounthEnd}, ${hoursEnd}:${minutesEnd}`;
  }
};
