import * as React from 'react';
import {
    AppBar,
    Container,
    IconButton,
    Tabs,
    Tab,
    Typography,
    Box,
    Button,
    List,
    ListItem,
    ListItemButton, ListItemText, Stack, Paper, Card, CardHeader, CardContent, CardMedia, CardActions, useTheme, Grid
} from "@mui/material";
import Time from "@mui/icons-material/AccessTime";
import Divider from '@mui/material/Divider';
import {generatePath, Link, useNavigate} from "react-router";
import axios, {AxiosResponse} from "axios";
import {baseUrl, eventsUrl, excursionsUrl} from "../../config/consts";
import {IPageResponse} from "../../common/response";
import {useEffect, useState} from "react";
import {Header} from "../../components/header/header-component";
import {ApplicationBar} from "../../components/appbar/application-bar";
import {eventItemRouteUrl, excursionItemRoute, placeRoute} from "../../config/routes";
import {Excursion} from "../excursion-page/model";
import {Event} from "../../common/event";
import {ReactComponent as Star} from "../../svg/star.svg";
import { EventItems } from './event-items.components';
import {formatDate} from "../../utils/date-formater";

export interface EventPageProps {

}


const EventPage = (props: EventPageProps) => {

    const navigate = useNavigate();

    const theme = useTheme();


    const [data, setData] = useState<Event[]>([]);

    useEffect(() => {
        axios.get(eventsUrl, {})
            .then((resp) => {
                setData(resp.data?.events || [])
            })
            .catch((resp) => {
                navigate("/")
            });
    }, [])

    return <div>
        <div style={{position: "relative"}}>
            <div style={{
                opacity: 0.03,
                position: "absolute",
                height: "100%",
                width: "100%",
                backgroundImage: "url(https://sun1-16.userapi.com/impg/7MVdJ7k4B_ik2HbKWyM68zx4gtGqYCNN1kJjaQ/p1oqti9iNfs.jpg?size=1600x1068&quality=96&sign=7fc8494393646b3ff6e56456854acce9&type=album)"
            }}>

            </div>
            <ApplicationBar/>
            <Header title={"События"}/>
        </div>
        <Stack spacing={4} sx={{
            [theme.breakpoints.down("md")]: {paddingTop: 4, paddingBottom: 4},
            [theme.breakpoints.down("sm")]: {paddingTop: 2, paddingBottom: 4},

            paddingTop: 6,
            paddingBottom: 6
        }}>
            <Paper  style={{background:"unset"}} sx={{backgroundColor: "unset"}}>
                <Container>
                    <Grid container rowSpacing={4} columnSpacing={4} paddingTop={4} paddingBottom={4}
                          flexDirection={"row"} flexWrap={"wrap"}>
                        {(data || []).map(it =>
                            <Grid size={{md: Math.max(4, 12/data.length), sm: Math.max(6, 12/data.length), xs: 12}}>
                                <Card sx={{height: "100%", position: "relative", paddingBottom: 6}}>
                                    <CardMedia
                                        src={it.image || ''}
                                        sx={{aspectRatio: "37 / 22"}}
                                        component="img"/>
                                    <CardContent>
                                        <Stack spacing={2} paddingTop={2}>
                                            <Typography variant={"h6"}>{it.title || ''}</Typography>
                                            {new Date(it.dateStart) < new Date() && (
                                                <Box sx={{width: {lg: "40%", md: "40%", sm: "45%", xs: "50%"}}}>
                                                    <Grid container alignItems="center" spacing={2}>
                                                        <Grid >
                                                            <Time/>
                                                        </Grid>
                                                        <Grid >
                                                            <Typography variant="h6">Уже идет</Typography>
                                                        </Grid>
                                                    </Grid>
                                                </Box>
                                            )}
                                            <Box>
                                                <EventItems
                                                    price={it.price}
                                                    place={it.place}
                                                    date={formatDate(new Date(it.dateStart), new Date(it.dateEnd))}
                                                    size={12}
                                                />
                                            </Box>
                                            <Typography
                                                variant={"subtitle1"} color={"textSecondary"}>{it.description || ''}</Typography>
                                        </Stack>
                                    </CardContent>
                                    <CardActions sx={{bottom: 0, position: "absolute"}}>
                                        <Button
                                            onClick={() => navigate(generatePath(eventItemRouteUrl, {id: `${it.id || ''}`}))}>Показать</Button>
                                    </CardActions>
                                    <Box sx={{position: "absolute", right: "0", bottom: "0", zIndex: 1}}>
                                        <Star/>
                                    </Box>
                                </Card>
                            </Grid>)
                        }
                    </Grid>
                </Container>
            </Paper>
        </Stack>
    </div>
}

export default EventPage;