import {Card, CardContent, Container, Paper, Stack, Typography, useTheme} from "@mui/material";
import * as React from "react";

export interface HeaderProps {
    title: string
}

export const Header = (props: HeaderProps) => {

    const theme = useTheme();

    return <Stack spacing={2}>
        <Paper>
            <Container>
                <Card sx={{
                    background: "unset",


                }}>
                    <CardContent
                        sx={{display: "flex", flexDirection: "column", width: "100%", alignContent: "center"}}>
                        <Typography variant={"h3"} textAlign={"center"} sx={{
                            [theme.breakpoints.up("md")]: {paddingTop: 4, paddingBottom: 4},
                            [theme.breakpoints.down("md")]: {paddingTop: 2, paddingBottom: 2},
                            [theme.breakpoints.down("sm")]: {paddingTop: 0, paddingBottom: 0},
                        }}>
                            {props.title}
                        </Typography>
                    </CardContent>
                </Card>
            </Container>
        </Paper>
    </Stack>
}