import {ExcursionPageProps} from "../excursion-page/excursion-page.component";
import {generatePath, useLocation, useMatch, useMatches, useNavigate, useParams} from "react-router";
import {useEffect, useState} from "react";
import axios, {AxiosResponse} from "axios";
import {baseUrl, excursionItemUrl, excursionsUrl, sightsUrl, topicItemUrl} from "../../config/consts";
import {IPageResponse, IResponse} from "../../common/response";
import {ApplicationBar} from "../../components/appbar/application-bar";
import {ReactComponent as Star} from "../../svg/star.svg";
import {
    Box,
    Button,
    Card,
    CardActions,
    CardContent, CardHeader,
    CardMedia,
    Container, Grid,
    Paper,
    Stack,
    Typography,
    useTheme
} from "@mui/material";
import * as React from "react";
import {YandexMap} from "../../components/map/map.component";
import {Qr} from "../../components/qrcode/qr-code.component";
import {placeRoute} from "../../config/routes";
import {ReactComponent as Background} from "../../svg/background.svg";
import {Topic} from "../excursion-item-page/model";

export interface TopicItemPageProps {

}

export const TopicItemPage = (props: TopicItemPageProps) => {
    const navigate = useNavigate();

    const id = useParams().id;

    const [data, setData] = useState<IResponse<Topic> | null>(null);

    const theme = useTheme();

    useEffect(() => {
        if (!id) {
            setData(null);
            return;
        }
        axios.get(generatePath(topicItemUrl, {id: id}), {
            params: {
                populate: "cover"
            }
        }).then((response: AxiosResponse<IResponse<Topic>>) => {
            const data = response.data;
            setData(data);
        }).catch((error) => navigate("/"));
    }, [id])

    if (!data) {
        return null;
    }

    const lats = data.data.attributes.places.data.map(it=>it.attributes.lat).filter(it=>!!it).map(it=>Number(it));
    const centerLat = lats.length>0 ? lats.reduce((prev, it)=> prev+it, 0)/lats.length : 0
    const lons = data.data.attributes.places.data.map(it=>it.attributes.lon).filter(it=>!!it).map(it=>Number(it));
    const centerLon = lons.length>0 ? lons.reduce((prev, it)=> prev+it, 0)/lons.length : 0

    return <div>
        <div style={{position: "relative"}}>
            <div style={{
                opacity: 0.03,
                position: "absolute",
                height: "100%",
                width: "100%",
                backgroundImage: "url(https://sun1-16.userapi.com/impg/7MVdJ7k4B_ik2HbKWyM68zx4gtGqYCNN1kJjaQ/p1oqti9iNfs.jpg?size=1600x1068&quality=96&sign=7fc8494393646b3ff6e56456854acce9&type=album)"
            }}>
            </div>
            <ApplicationBar/>
        </div>

        <Stack spacing={4} sx={{
            [theme.breakpoints.down("md")]: {paddingTop: 4},
            [theme.breakpoints.down("sm")]: {paddingTop: 2},

            paddingTop: 4
        }}>
            <Paper style={{background:"unset"}} sx={{backgroundColor: "unset"}}>
                <Container>
                    <Stack flexDirection={"row-reverse"} alignItems={"center"}>
                        <Box sx={{[theme.breakpoints.down("lg")]: {display: "none"}}}>
                            <Qr url={window.location.href}/>
                        </Box>
                        <Stack spacing={4} sx={{
                            flex:1,
                            [theme.breakpoints.down("md")]: {paddingTop: 4},
                            [theme.breakpoints.down("sm")]: {paddingTop: 2},
                        }}>
                            <Typography variant={"h5"}>{data.data.attributes.caption || ''}</Typography>
                            <Typography variant={"subtitle1"} color={"textSecondary"} >{data.data.attributes.description}</Typography>
                        </Stack>
                    </Stack>

                </Container>
            </Paper>
            {(centerLat && centerLon) ?
                <Paper sx={{paddingTop: 4, paddingBottom: 4,  position:"relative"}}>
                    <Box sx={{position:"absolute", top:0, zIndex:0, right:0, bottom:0, left:0}}>
                        <Background style={{width:"100%", height:"100%"}}/>
                    </Box>
                    <Container>
                        <Stack spacing={4} sx={{
                            [theme.breakpoints.down("md")]: {paddingTop: 4},
                            [theme.breakpoints.down("sm")]: {paddingTop: 2},
                        }}>
                            <YandexMap
                                center={{
                                    lon: centerLat,
                                    lat: centerLon
                                }}
                                disableControls={true}
                                places={data.data?.attributes?.places?.data || []}
                                onPlaceClick={() => {
                                }}
                                style={{
                                    height: 400,
                                }}
                                zoom={13}
                                route={[]}
                                isExcursion={false}
                            />
                        </Stack>
                    </Container>
                </Paper>
                : null}
            {!(data?.data?.attributes.places.data) ? null : <Paper  style={{background:"unset"}} sx={{backgroundColor: "unset"}}>
                <Container>
                    <Stack spacing={4} paddingTop={4} paddingBottom={4} sx={{
                        [theme.breakpoints.down("md")]: {paddingTop: 4},
                        [theme.breakpoints.down("sm")]: {paddingTop: 2},
                        textAlign: "left"
                    }}>
                        <Typography variant={"h5"}>Состав маршрута</Typography>
                    </Stack>
                    <Grid container rowSpacing={4} columnSpacing={4} paddingTop={4} paddingBottom={4}
                          flexDirection={"row"} flexWrap={"wrap"}>
                        {(data?.data?.attributes.places.data || []).map(it =>
                            <Grid size={{md: 6, sm: 6, xs: 12}}>
                                <Card sx={{height: "100%", position: "relative", paddingBottom: 6}}>
                                    <CardMedia
                                        src={it.attributes.pictures[0].image?.data?.attributes?.formats?.small?.url && `${baseUrl}/${it.attributes.pictures[0].image?.data?.attributes?.formats?.small?.url}` || ''}
                                        sx={{aspectRatio: "37 / 22"}}
                                        component="img"/>
                                    <CardContent>
                                        <Stack spacing={2} paddingTop={2}>
                                            <Typography variant={"h6"}>{it.attributes.caption || ''}</Typography>
                                            <Typography
                                                variant={"subtitle1"} color={"textSecondary"}>{it.attributes.description || ''}</Typography>
                                        </Stack>
                                    </CardContent>
                                    <CardActions sx={{bottom: 0, position: "absolute"}}>
                                        <Button onClick={() => navigate(generatePath(placeRoute, {id:`${it.id || ''}`}))}>Показать</Button>
                                    </CardActions>
                                    <Box sx={{position:"absolute", right:"0", bottom:"0", zIndex:1}}>
                                        <Star />
                                    </Box>
                                </Card>
                            </Grid>)
                        }
                    </Grid>
                </Container>
            </Paper>}
        </Stack>
    </div>
}