import React from 'react';
import './App.css';
import MainPage from "./pages/main-page/main-page.component";
import {ThemeProvider} from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import { darkTheme} from "./theme/dark.theme";
import {createBrowserRouter, RouterProvider} from "react-router";
import {
    aboutRouteUrl,
    eventItemRouteUrl,
    eventsRouteUrl,
    excursionItemRoute,
    excursionRoute,
    placeRoute,
    topicRouteUrl,
    topicsRouteUrl
} from "./config/routes";
import ExcursionPage from "./pages/excursion-page/excursion-page.component";
import {ExcursionItemPage} from "./pages/excursion-item-page/excursion-item-page.component";
import {YMaps} from "@pbe/react-yandex-maps";
import {PlaceItemPage} from "./pages/place-item-page/place-item-page.component";
import TopicPage from "./pages/topic-page/topic-page.component";
import {TopicItemPage} from "./pages/topic-item-page/topic-item-page.component";
import EventPage from "./pages/events-page/events-page.component";
import {EventItemPage} from "./pages/event-item-page/events-item-page.component";
import {AboutPage} from "./pages/about-page/about-page.component";

const router = createBrowserRouter([
    {
        path: `${process.env.PUBLIC_URL}/`,
        element: <MainPage/>,
    },
    {
        path: excursionRoute,
        element: <ExcursionPage/>,
    },
    {
        path: excursionItemRoute,
        element: <ExcursionItemPage/>,
    }, {
        path: placeRoute,
        element: <PlaceItemPage/>,
    },{
        path: topicsRouteUrl,
        element: <TopicPage/>,
    },{
        path: topicRouteUrl,
        element: <TopicItemPage/>,
    },{
        path: eventsRouteUrl,
        element: <EventPage/>,
    },{
        path: eventItemRouteUrl,
        element: <EventItemPage/>,
    },{
        path: aboutRouteUrl,
        element: <AboutPage/>,
    },
]);


function App() {
    return (
        <YMaps version={"2.1.79"}>
            <ThemeProvider theme={darkTheme}>
                <CssBaseline/>
                <RouterProvider router={router}/>
            </ThemeProvider>
        </YMaps>
    )
}

export default App;
