import {generatePath, useLocation, useNavigate, useParams} from "react-router";
import {useEffect, useState} from "react";
import {IResponse} from "../../common/response";
import {
    Box, Button,
    Card,
    CardActions,
    CardContent,
    CardMedia,
    Container, Fade,
    Grid, Icon, IconButton, ImageList, ImageListItem, ImageListItemBar, ListItem, ListItemAvatar,
    Paper,
    Stack,
    Typography,
    useTheme
} from "@mui/material";
import axios, {AxiosResponse} from "axios";
import {baseUrl, excursionItemUrl, placeItemUrl} from "../../config/consts";
import {ApplicationBar} from "../../components/appbar/application-bar";
import {Qr} from "../../components/qrcode/qr-code.component";
import {YandexMap} from "../../components/map/map.component";
import {excursionItemRoute, excursionRoute, placeRoute, topicRouteUrl} from "../../config/routes";
import * as React from "react";
import {Place} from "../excursion-item-page/model";
import {Camera, Photo, PhotoCamera, TipsAndUpdates} from "@mui/icons-material";
    import {ReactComponent as Background} from "../../svg/background.svg"
    import {ReactComponent as Background2} from "../../svg/background-2.svg"
    import {ReactComponent as Background3} from "../../svg/background-3.svg"

export interface PlaceItemPageProps {

}

export const PlaceItemPage = (props: PlaceItemPageProps) => {
    const navigate = useNavigate();

    const id = useParams().id;

    const [selectedPicture, setSelectedPicture] = useState(0);


    const [data, setData] = useState<IResponse<Place> | null>(null);

    const theme = useTheme();

    useEffect(() => {
        if (!id) {
            setData(null);
            return;
        }
        axios.get(generatePath(placeItemUrl, {id: id}), {
            params: {
                populate: "cover"
            }
        }).then((response: AxiosResponse<IResponse<Place>>) => {
            const data = response.data;
            setData(data);
        }).catch((error) => navigate("/"));
    }, [id])

    if (!data) {
        return null;
    }

    return <div>
        <div style={{position: "relative"}}>
            <div style={{
                opacity: 0.03,
                position: "absolute",
                height: "100%",
                width: "100%",
                backgroundImage: "url(https://sun1-16.userapi.com/impg/7MVdJ7k4B_ik2HbKWyM68zx4gtGqYCNN1kJjaQ/p1oqti9iNfs.jpg?size=1600x1068&quality=96&sign=7fc8494393646b3ff6e56456854acce9&type=album)"
            }}>
            </div>
            <ApplicationBar/>
        </div>

        <Stack spacing={4} sx={{
            [theme.breakpoints.down("md")]: {paddingTop: 4},
            [theme.breakpoints.down("sm")]: {paddingTop: 2},

            paddingTop: 4
        }}>
            <Paper style={{background:"unset"}} sx={{backgroundColor: "unset"}}>
                <Container>
                    <Stack flexDirection={"row-reverse"} alignItems={"center"}>
                        <Box sx={{[theme.breakpoints.down("lg")]: {display: "none"}}}>
                            <Qr url={window.location.href}/>
                        </Box>
                        <Stack spacing={4} sx={{
                            [theme.breakpoints.down("md")]: {paddingTop: 4},
                            [theme.breakpoints.down("sm")]: {paddingTop: 2},
                            flex: 1
                        }}>
                            <Typography variant={"h5"}>{data.data.attributes.caption || ''}</Typography>
                            <Typography variant={"subtitle1"} color={"textSecondary"} >{data.data.attributes.description}</Typography>
                        </Stack>
                    </Stack>

                </Container>
            </Paper>
            {(data?.data?.attributes?.pictures && <Paper sx={{paddingTop: 4, paddingBottom: 4, position:"relative"}}>
                <Box sx={{position:"absolute", top:0, zIndex:0, right:0, bottom:0, left:0}}>
                      <Background style={{width:"100%", height:"100%"}}/>
                </Box>
                <Container>
                    <Stack flexDirection={"row"} sx={{}} paddingLeft={4} paddingRight={4}>
                        <ImageListItem sx={{flex: 1}}>
                            <Fade in={true}
                                  key={data?.data?.attributes?.pictures[selectedPicture].image?.data.id || selectedPicture}>
                                <img
                                    srcSet={`${baseUrl}/${data?.data?.attributes?.pictures[selectedPicture].image?.data?.attributes?.formats?.large?.url || ''}`}
                                    src={`${data?.data?.attributes?.pictures[selectedPicture].image?.data?.attributes?.formats?.large?.url || ''}`}
                                    width={"100%"}
                                    loading="lazy"
                                />
                            </Fade>
                            <ImageListItemBar
                                title={data?.data?.attributes?.pictures[selectedPicture].image?.data?.attributes?.caption}
                                subtitle={<ListItem><ListItemAvatar sx={{minWidth: 30}}><PhotoCamera sx={{
                                    width: 14,
                                    height: 14
                                }}/></ListItemAvatar>{data?.data?.attributes?.pictures[selectedPicture].author?.data?.attributes?.lastName || ''}{' '}{data?.data?.attributes?.pictures[selectedPicture].author?.data?.attributes?.firstName || ''}
                                </ListItem>}
                            />
                        </ImageListItem>
                        <Box>
                            <ImageList sx={{paddingLeft: 1, marginBlockStart: 0, marginBlockEnd: 0}} cols={1}
                                       rowHeight={100}>
                                {(data?.data?.attributes?.pictures || []).map((item, idx) => (
                                    <ImageListItem key={item.id}
                                                   sx={{aspectRatio: "37 /37", ":hover": {cursor: "pointer"}}}
                                                   onClick={() => setSelectedPicture(idx)}>
                                        <img
                                            srcSet={`${baseUrl}/${item.image?.data?.attributes?.formats?.large?.url || ''}`}
                                            src={`${item.image?.data?.attributes?.formats?.large?.url || ''}`}
                                            alt={item.image?.data?.attributes?.caption}
                                            loading="lazy"
                                        />
                                    </ImageListItem>
                                ))}
                            </ImageList>
                        </Box>
                    </Stack>
                </Container>
            </Paper>)}
            {(data?.data?.attributes?.facts && <Paper style={{background:"unset"}} sx={{paddingTop: 4, paddingBottom: 4, backgroundColor: "unset"}}>
                <Container>

                    <Stack spacing={4} sx={{
                        [theme.breakpoints.down("md")]: {paddingTop: 4},
                        [theme.breakpoints.down("sm")]: {paddingTop: 2},
                        flex: 1
                    }}>
                        <Typography variant={"h5"}>Интересные факты</Typography>
                    </Stack>
                    <Grid container
                          paddingTop={4}
                          paddingBottom={4}>
                        {data.data?.attributes?.facts.map(fact =>
                            <Grid size={4}>
                                <Card style={{background:"unset"}} sx={{background: "unset", flex: 1}}>
                                    <CardContent
                                        sx={{
                                            display: "flex",
                                            flexDirection: "column",
                                            width: "100%",
                                            alignContent: "center"
                                        }}>
                                        <CardMedia>
                                            <Box sx={{
                                                width: 60,
                                                height: 60,
                                                display: "flex",
                                                alignContent: "center",
                                                alignItems: "center",
                                                justifyContent: "center",
                                                backgroundColor: theme.palette.primary.dark,
                                                borderRadius: 2
                                            }}>
                                                <Icon>
                                                    <TipsAndUpdates sx={[({theme}:any)=>({
                                                        color: "white"
                                                    })]}/>
                                                </Icon>
                                            </Box>
                                        </CardMedia>
                                        <Typography variant={"subtitle2"} textAlign={"left"} paddingTop={4}
                                                    paddingBottom={4} color={"textPrimary"}>
                                            {fact.description || ''}
                                        </Typography>

                                    </CardContent>
                                </Card>
                            </Grid>
                        )}
                    </Grid>
                </Container>
            </Paper>)}
            {(data?.data?.attributes?.lat && data?.data?.attributes?.lon) ?
                <Paper sx={{paddingTop: 4, paddingBottom: 4, position:"relative"}}>
                    <Box sx={{position:"absolute", top:0, zIndex:0, right:0, bottom:0, left:0}}>
                        <Background style={{width:"100%", height:"100%"}}/>
                    </Box>
                    <Container>
                        <Stack spacing={4} sx={{
                            [theme.breakpoints.down("md")]: {paddingTop: 4},
                            [theme.breakpoints.down("sm")]: {paddingTop: 2},
                        }}>
                            <YandexMap
                                center={{
                                    lat: Number(data.data.attributes.lon),
                                    lon: Number(data.data.attributes.lat)
                                }}
                                disableControls={true}
                                onPlaceClick={() => {
                                }}
                                style={{
                                    height: 300,
                                }}
                                places={[data.data]}
                                zoom={14}
                                isExcursion={false}
                                route={[]}
                            />
                        </Stack>
                    </Container>
                </Paper>
                : null}
            {!(data?.data?.attributes.topics?.data) ? null : <Paper style={{background:"unset"}} sx={{backgroundColor: "unset", position:"relative"}}>
                <Box sx={{position:"absolute", top:0, zIndex:0, right:0, bottom:0, left:0, overflow:"hidden"}}>
                    <Background2 style={{width:"100%", height:"100%", minHeight:600}}/>
                </Box>
                <Container>
                    <Stack spacing={4} paddingTop={4} paddingBottom={4} sx={{
                        [theme.breakpoints.down("md")]: {paddingTop: 4},
                        [theme.breakpoints.down("sm")]: {paddingTop: 2},

                    }}>
                        <Typography variant={"h5"}>Другие рубрики</Typography>
                    </Stack>
                    <Grid container rowSpacing={4} columnSpacing={4} paddingTop={4} paddingBottom={4}
                          flexDirection={"row"} flexWrap={"wrap"}>
                        {(data?.data?.attributes.topics.data || []).map(it =>
                            <Grid size={6}>
                                <Card sx={{display: 'flex', flex: 1, padding: theme.spacing(2)}}>
                                    <CardMedia
                                        component="img"
                                        sx={{width: 120}}
                                        src={it.attributes.cover?.data.attributes?.formats?.small?.url && `${baseUrl}/${it.attributes.cover?.data.attributes?.formats?.small?.url}` || ''}

                                    />
                                    <Box sx={{display: 'flex', flexDirection: 'column'}}>
                                        <CardContent sx={{flex: '1 0 auto'}}>
                                            <Stack spacing={2} paddingTop={0}>
                                                <Typography variant={"h6"}>{it.attributes.caption || ''}</Typography>
                                            </Stack>
                                        </CardContent>
                                        <CardActions>
                                            <Button
                                                onClick={() => navigate(generatePath(topicRouteUrl, {id: `${it.id}`}))}>Показать</Button>
                                        </CardActions>
                                    </Box>

                                </Card>
                            </Grid>)

                        }
                    </Grid>
                </Container>
            </Paper>}
            {!(data?.data?.attributes.excursions?.data) ? null : <Paper style={{background:"unset"}} sx={{backgroundColor: "unset", position:"relative"}}>
                <Box sx={{position:"absolute", top:0, zIndex:0, right:0, bottom:0, left:0, overflow:"hidden"}}>
                    <Background3 style={{width:"100%", height:"100%", minHeight:600}}/>
                </Box>
                <Container>
                    <Stack spacing={4} paddingTop={4} paddingBottom={4} sx={{
                        [theme.breakpoints.down("md")]: {paddingTop: 4},
                        [theme.breakpoints.down("sm")]: {paddingTop: 2},
                    }}>
                        <Typography variant={"h5"}>Экскурсии</Typography>
                    </Stack>
                    <Grid container rowSpacing={4} columnSpacing={4} paddingTop={4} paddingBottom={4}
                          flexDirection={"row"} flexWrap={"wrap"}>
                        {(data?.data?.attributes.excursions.data || []).map(it =>
                            <Grid size={6}>
                                <Card sx={{
                                    display: 'flex',
                                    padding: theme.spacing(2)
                                }}>
                                    <CardMedia
                                        component="img"
                                        sx={{width: 120, aspectRatio: "1 / 1"}}
                                        src={it.attributes.cover?.data.attributes?.formats?.small?.url && `${baseUrl}/${it.attributes.cover?.data.attributes?.formats?.small?.url}` || ''}

                                    />
                                    <Box sx={{display: 'flex', flexDirection: 'column'}}>
                                        <CardContent sx={{
                                            flex: '1 0 auto',
                                            overflow: "hidden",
                                            whiteSpace: "nowrap",
                                            textOverflow: "ellipsis"
                                        }}>
                                            <Stack spacing={2} paddingTop={0}>
                                                <Typography variant={"h6"}>{it.attributes.caption || ''}</Typography>
                                            </Stack>
                                        </CardContent>
                                        <CardActions>
                                            <Button
                                                onClick={() => navigate(generatePath(excursionItemRoute, {id: `${it.id}`}))}>Показать</Button>
                                        </CardActions>
                                    </Box>

                                </Card>
                            </Grid>)

                        }
                    </Grid>
                </Container>
            </Paper>}
        </Stack>
    </div>
}