import React, {useState, useMemo, useContext} from "react";
import {Box, styled} from "@mui/material";

import {Placemark, Polyline, Map as YMap, Clusterer, useYMaps} from "@pbe/react-yandex-maps";
import {Place} from "../../pages/excursion-item-page/model";
import {baseUrl} from "../../config/consts";


interface Coords {
    lat: number;
    lon: number;
}

export interface MapStyles {
}

interface MapProps {
    center: Coords;
    places: Place[];
    onPlaceClick: Function;
    style: MapStyles;
    className?: string
    zoom: number;
    isExcursion: boolean;
    route: [number, number][];
    disableControls?: boolean
}

interface Limits {
    minLat: number;
    maxLat: number;
    minLon: number;
    maxLon: number;
}

export const RootDiv = styled("div", {
    name: 'YandexMap',
    slot: 'root'
})(({theme}) => ({}))


export const YandexMap = (props: MapProps) => {


    const [centerPos, setCenterPos] = useState<Coords>(props.center);
    const [zoomState, setZoomState] = useState<number>(props.zoom);
    const allPalaces: Place[] = useMemo(() => {
        return props.places || [];
    }, [props.places]);

    const [clName, setClName] = useState('');

    const limitations: Limits = useMemo(() => {
        return {
            minLat: allPalaces.length > 0 ?
                allPalaces.filter(it => it.attributes.lat).map((item) => Number(item.attributes.lat))
                    .reduce((prev, cur) => (prev < cur ? prev : cur)) : props.center.lat,
            maxLat: allPalaces.length > 0 ? allPalaces.filter(it => it.attributes.lat).map((item) => Number(item.attributes.lat))
                .reduce((prev, cur) => (prev > cur ? prev : cur)) : props.center.lat,
            minLon: allPalaces.length > 0 ? allPalaces.filter(it => it.attributes.lon).map((item) => Number(item.attributes.lon))
                .reduce((prev, cur) => (prev < cur ? prev : cur)) : props.center.lon,
            maxLon: allPalaces.length > 0 ? allPalaces.filter(it => it.attributes.lon).map((item) => Number(item.attributes.lon))
                .reduce((prev, cur) => (prev > cur ? prev : cur)) : props.center.lon
        };
    }, [allPalaces]);


    const navigateOnMap = (direction: string) => {
        return () => {
            const offset = 0.005 + (14 - zoomState) * 0.001;

            switch (direction) {
                case "top":
                    if (!(centerPos.lat - 0.01 > limitations.maxLat)) {
                        setCenterPos((prev) => ({lat: prev.lat + offset, lon: prev.lon}));
                    }
                    break;
                case "left":
                    if (!(centerPos.lon + 0.01 < limitations.minLon)) {
                        setCenterPos((prev) => ({lat: prev.lat, lon: prev.lon - offset}));
                    }
                    break;
                case "bottom":
                    if (!(centerPos.lat + 0.01 < limitations.minLat)) {
                        setCenterPos((prev) => ({lat: prev.lat - offset, lon: prev.lon}));
                    }
                    break;
                case "right":
                    if (!(centerPos.lon - 0.01 > limitations.maxLon)) {
                        setCenterPos((prev) => ({lat: prev.lat, lon: prev.lon + offset}));
                    }
                    break;
            }
        };
    };
    const incrZoom = () => {
        setZoomState((prev) => ++prev);
    };
    const decrZoom = () => {
        setZoomState((prev) => --prev);
    };

    const extraProps = !props.disableControls && {
        restrictMapArea: true,
        suppressMapOpenBlock: true,
        suppressObsoleteBrowserNotifier: true,
        behaviors: [],
        controls: [],
    } || {};


    return (
        <RootDiv>
            <Box sx={{
                width: "100%",
                height: {
                    xs: 400,
                    sm: 500,
                    md: 600
                },
                position: "relative" as any,
            }}>


                <YMap
                    key={"sf"}
                    options={{
                        suppressMapOpenBlock: true,
                        suppressObsoleteBrowserNotifier: true,
                    }}
                    className={"Inn"}
                    theme={"dark"}
                    style={{
                        borderRadius: 10,
                        width: "100%",
                        height: "100%",
                    }}
                    state={{
                        center: [centerPos.lat, centerPos.lon],
                        zoom: zoomState,
                        ...extraProps
                    }}
                >
                    {props.isExcursion && (
                        <Polyline
                            geometry={props.route}
                            options={{
                                //balloonCloseButton: true,
                                strokeColor: "#ed4543",
                                strokeWidth: 8,
                                strokeOpacity: 0.5,
                            }}
                        />
                    )}
                    <Clusterer
                        options={{
                            preset: "islands#invertedVioletClusterIcons",
                            groupByCoordinates: false,
                        }}
                    >
                        {props.places.filter(it => it.attributes?.lat && it.attributes?.lon).map((it) => (
                            <Placemark
                                key={it.id}
                                className={'aaaa'}
                                instanceRef={(ref: any) => {
                                    (window as any).plref = ref;
                                    ref && ref.events.add("click", props.onPlaceClick(it));
                                }}
                                options={{
                                    // Опции.
                                    // Необходимо указать данный тип макета.
                                    iconLayout: 'default#image',
                                    // Своё изображение иконки метки.
                                    iconImageHref: it.attributes.pictures[0].image?.data?.attributes?.formats?.small?.url && `${baseUrl}/${it.attributes.pictures[0].image?.data?.attributes?.formats?.small?.url}` || '',
                                    // Размеры метки.
                                    iconImageSize: [50, 50],
                                    iconImageOffset: [-25, -25],
                                    iconShape: {
                                        type: 'Circle',
                                        coordinates: [0, 0],
                                        radius: 10,

                                    } as any
                                }}
                                geometry={[Number(it.attributes.lon), Number(it.attributes.lat)]}
                            />
                        ))}
                    </Clusterer>
                </YMap>
            </Box>
        </RootDiv>
    );
};
