import {
    AppBar,
    Box,
    Button,
    Container,
    IconButton,
    Menu,
    MenuItem,
    Typography, useColorScheme,
    useMediaQuery,
    useTheme
} from "@mui/material";
import Toolbar from "@mui/material/Toolbar";
import {pages} from "../../config/pages";
import * as React from "react";
import {DarkMode, LightMode, Menu as MenuIcon} from "@mui/icons-material";
import {useEffect, useRef, useState} from "react";
import {Link, useNavigate} from "react-router";


export const ApplicationBar = () => {

    const theme = useTheme();

    const menuRef = useRef<HTMLAnchorElement | HTMLButtonElement | null>(null);

    const [menuOpened, setMenuOpened] = useState(false);

    const isSmall = useMediaQuery(theme.breakpoints.down('md'));

    const navigate = useNavigate();

    const {mode, setMode} = useColorScheme();

    useEffect(() => {
        setMenuOpened(false)
    }, [isSmall])

    return <div>
        <AppBar position="static" variant={"outlined"} sx={{border: "none", visibility: "hidden"}}>
            <Container>
                <Toolbar disableGutters>
                    <Typography variant="h6" color="inherit" noWrap
                                sx={{
                                    mr: 2,
                                    //  fontFamily: 'monospace',
                                    fontWeight: 700,
                                    letterSpacing: '.3rem',
                                    color: 'inherit',
                                    textDecoration: 'none',
                                    flexGrow: 1,
                                    display: {xs: 'none', sm: 'block'}
                                }}>
                        Гид по Омску
                    </Typography>
                </Toolbar>
            </Container>

        </AppBar>
        <AppBar position="fixed" variant={"outlined"}
                sx={{border: "none", backgroundColor: theme.palette.background.paper}}>
            <Container>
                <Toolbar disableGutters>
                    <Typography variant="h6" color="inherit" noWrap
                                sx={{
                                    [theme.breakpoints.down('md')]: {
                                        textAlign: 'center',
                                        marginRight: 0
                                    },
                                    mr: 2,
                                    //  fontFamily: 'monospace',
                                    fontWeight: 700,
                                    letterSpacing: '.3rem',
                                    color: 'inherit',
                                    textDecoration: 'none',
                                    flexGrow: 1,
                                    display: 'block'
                                }}>
                        <Link to={"/"} style={{textDecoration: "unset"}}>
                            <Typography variant={"h5"} sx={{color: theme.palette.primary.light, fontWeight: 700, fontSize: "1.5rem"}}>
                                Гид по Омску
                            </Typography>
                        </Link>
                    </Typography>
                    <Box sx={{[theme.breakpoints.down('md')]: {display: 'none'}, display: 'block'}}>
                        {pages.map((item) => (
                            <Button key={item.id} onClick={() => navigate(item.route)}>
                                {item.title}
                            </Button>
                        ))}
                        <IconButton color={"primary"} sx={{paddingLeft: 2, paddingRight: 2}}>
                            {mode === 'dark' && <LightMode onClick={() => setMode("light")}/>}
                            {mode !== 'dark' && <DarkMode onClick={() => setMode("dark")}/>}
                        </IconButton>
                    </Box>

                    <Box sx={{
                        [theme.breakpoints.up('md')]: {display: 'none'},
                        display: 'flex',
                        position: "absolute",
                        right: 0,
                        flexDirection: "row-reverse"
                    }}>
                        <IconButton onClick={() => setMenuOpened(true)} ref={(el) => {
                            menuRef.current = el
                        }}>
                            <MenuIcon/>
                        </IconButton>
                        <Menu anchorEl={menuRef.current} open={menuOpened} onClick={() => setMenuOpened(false)}>
                            {pages.map((item) => (
                                <MenuItem key={item.id} onClick={() => navigate(item.route)}>
                                    {item.title}
                                </MenuItem>
                            ))}
                        </Menu>
                    </Box>
                </Toolbar>
            </Container>

        </AppBar>
    </div>
}