import * as React from 'react';
import {
    AppBar,
    Container,
    IconButton,
    Tabs,
    Tab,
    Typography,
    Box,
    Button,
    List,
    ListItem,
    ListItemButton, ListItemText, Stack, Paper, Card, CardHeader, CardContent, CardMedia, CardActions, useTheme
} from "@mui/material";
import Divider from '@mui/material/Divider';
import {generatePath, Link, useNavigate} from "react-router";
import axios, {AxiosResponse} from "axios";
import {baseUrl, excursionsUrl} from "../../config/consts";
import {IPageResponse} from "../../common/response";
import {Excursion} from "./model";
import {useEffect, useState} from "react";
import {Header} from "../../components/header/header-component";
import {ApplicationBar} from "../../components/appbar/application-bar";
import {excursionItemRoute} from "../../config/routes";

export interface ExcursionPageProps {

}


const ExcursionPage = (props: ExcursionPageProps) => {

    const navigate = useNavigate();

    const theme = useTheme();


    const [data, setData] = useState<IPageResponse<Excursion>>({
        data: [],
        meta: {
            pagination: {
                page: 0,
                total: 0,
                pageCount: 0,
                pageSize: 0
            }
        }
    });

    useEffect(() => {
        axios.get(excursionsUrl, {
            params: {
                populate: "cover"
            }
        }).then((response: AxiosResponse<IPageResponse<Excursion>>) => {
            const data = response.data;
            setData(data);
        }).catch((error) => navigate("/"));
    }, [])

    return <div>
        <div style={{position: "relative"}}>
            <div style={{
                opacity: 0.03,
                position: "absolute",
                height: "100%",
                width: "100%",
                backgroundImage: "url(https://sun1-16.userapi.com/impg/7MVdJ7k4B_ik2HbKWyM68zx4gtGqYCNN1kJjaQ/p1oqti9iNfs.jpg?size=1600x1068&quality=96&sign=7fc8494393646b3ff6e56456854acce9&type=album)"
            }}>

            </div>
            <ApplicationBar/>
            <Header title={"Экскурсии"}/>
        </div>
        <Stack spacing={4} sx={{
            [theme.breakpoints.down("md")]: {paddingTop: 4},
            [theme.breakpoints.down("sm")]: {paddingTop: 2},

            paddingTop: 8
        }}>
            {(data.data || []).map((it, idx) => <Paper key={idx} style={{background: "none"}}>
                <Container>
                    <Card sx={{ flex: 1, display: {xs: "none", sm: "none", md: "flex"}}}>
                        <CardMedia
                            component="img"
                            sx={{flex: 1, width: "40%"}}
                            image={baseUrl + (it.attributes?.cover?.data?.attributes?.formats?.medium?.url || '')}
                        />
                        <CardContent
                            sx={[{
                                display: "flex",
                                flexDirection: "column",
                                flex: 2,
                                width: "50%"
                            }]}>

                            <Typography variant={"h5"} padding={4}>
                                {it.attributes?.caption || ''}
                            </Typography>

                            <Divider/>
                            <Typography variant={"subtitle1"} textAlign={"justify"} color={"textSecondary"} paddingTop={4} paddingBottom={4}>
                                {it.attributes?.description || ''}
                            </Typography>
                            <CardActions sx={{padding: 0}} onClick={()=>navigate(generatePath(excursionItemRoute, {id: `${it.id}`}))}>
                                <Button>Показать</Button>
                            </CardActions>
                        </CardContent>
                    </Card>
                    <Card sx={{
                        [theme.breakpoints.down("md")]: {display: "flex"},
                        display: "none",
                        width: "100%",
                        flex: 1,
                        flexDirection: "column"
                    }}>
                        <CardContent
                            sx={[{
                                display: "block",
                                flexDirection: "column",
                                width: "100%"
                            }]}>
                            <Typography variant={"h4"} textAlign={"center"} padding={4}>
                                {it.attributes?.caption || ''}
                            </Typography>
                        </CardContent>
                        <CardMedia
                            component="img"
                            sx={{width: "100%"}}
                            image={baseUrl + (it.attributes?.cover?.data?.attributes?.formats?.medium?.url || '')}
                        />
                        <CardContent
                            sx={[{
                                display: {sm: "block", md: "flex"},
                                flexDirection: "column",
                                width: "100%"
                            }]}>

                            <Typography variant={"h6"} textAlign={"justify"} paddingTop={4} paddingBottom={4}
                                        color={"textSecondary"}>
                                {it.attributes?.description || ''}
                            </Typography>
                            <CardActions sx={{padding: 0}}>
                                <Button>Показать</Button>
                            </CardActions>
                        </CardContent>

                    </Card>
                </Container>
            </Paper>)}
        </Stack>
    </div>
}

export default ExcursionPage;